.Header {
    display: flex;
    justify-content: center;
    align-items: center;

    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 50px;

    background-color: rgba(0,0,0,.9);
}

.Header__button {
    position: absolute;
    right: 0;
    top: 0;

    height: 50px;
    padding: 0 20px;

    border: 0;
    outline: 0;

    background-color: transparent;

    color: var(--font-color-main);
    font-size: 1.2rem;
    text-transform: uppercase;
    line-height: 50px;
}

.Header__menu {
    position: fixed;
    top: 50px;

    width: 100%;
    height: calc(100vh - 50px);

    border-top: 1px solid rgba(67, 67, 67, .9);

    background-color: rgba(0, 0, 0, .9);
}

.Header__loading {
    padding: 20px 0;

    text-align: center;
    font-size: 1.6rem;
}

.City {
    display: flex;
    align-items: flex-end;

    width: 100%;
    min-height: 100vh;

    background-repeat: no-repeat;
    background-size: cover;
}

.City__body {
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 100%;
    min-height: 40vh;

    padding: 60px 30px 20px;

    background: -webkit-linear-gradient(top, transparent 20%, rgba(0,0,0,1));

    background: linear-gradient(to bottom, transparent 20%, rgba(0,0,0,1));
}

.City__name {
    margin: 0;

    font-size: 5rem;
    font-family: var(--font-family-title);
    letter-spacing: 1px;
}

.City__subtitle {
    margin-top: 40px;
    margin-bottom: 0;

    font-size: 2rem;
}

.City__date {
    font-size: 2rem;
}

@media screen and (min-width: 640px) {
    .City__body {
        padding-left: 50px;
        padding-bottom: 50px;    
    }

    .City__name {
        font-size: 12rem;
    }

    .City__subtitle {
        font-size: 3rem;
    }

    .City__date {
        font-size: 3rem;
    }
}

